import "./index.scss"
import React, { Component } from "react"
import Head from "~/components/head"
import { history } from "&"
import car from "../../mobx/car"
import { observer } from "mobx-react"
import cartImg from "@/assets/images/cart.jpg"
import { NoticeBar, Checkbox, Flex } from "antd-mobile"
const AgreeItem = Checkbox.AgreeItem;

@observer
class Cart extends Component {

    componentWillMount() {

        var mobile = sessionStorage.mobile
        car.getCartInfo(mobile)
        console.log(car.cartInfo)
    }

    goToHome() {
        history.push("/home");

    }


    changeCount = (goodsName, count, i) => {
        // console.log(goodsName)
        if (this[i].value > 0) {
            if (count == 1) {
                this[i].value++
                car.changeCount(goodsName, count * 1)

            } else if (count == -1) {
                this[i].value--
                car.changeCount(goodsName, count * 1)

                if (!this[i].value > 0) {
                    return this[i].value = 1
                }
            }
        }
        setTimeout(() => {
            window.location.reload()

        }, 800)
    }

    render() {
        const {
            getCartInfo,
            cartInfo,
            changeCount,
            count,
            price
        } = car


        return (
            <div className="cart">
                <Head title="购物车"></Head>


                <div className="carbox">
                    <div className="box_common">
                        <NoticeBar clear="true" marqueeProps={{ loop: true, style: { padding: '0 7.5px' } }}>
                            好消息好消息！  1/18号就可以放假回家 希望大家2020年可以变的更优秀  daydayup
                                </NoticeBar>
                        <div >
                            <Flex >
                                <Flex.Item >
                                    <AgreeItem className="ckechquan" data-seed="logId" onChange={e => console.log('checkbox', e)}>出货
                                <div style={{ color: "#ff6692", marginLeft: "3.4rem", }}>满￥68元免运费</div>

                                    </AgreeItem>
                                </Flex.Item>
                            </Flex>
                        </div>
                        {
                            car.cartInfo &&
                            car.cartInfo.map((item, i) => (

                                < div className="box_goods" key={i}>

                                    <Flex>
                                        <Flex.Item>
                                            <AgreeItem className="ckechquan" data-seed="logId" onChange={e => console.log('checkbox', e)}>

                                                <a onClick={(e) => { e.preventDefault() }}>   <img src={item.over_image_url} alt="" />
                                                    <div className="box_item">
                                                        <div className="tit1">{item.item_name} </div>
                                                        <div className="tit2">{item.sale_point}</div>
                                                    </div>
                                                    <div className="comp">
                                                        <div className="comp_price">￥{(item.min_price / 100 * item.count).toFixed(2)}</div>
                                                        {/* <div className="comp_count">×11</div> */}
                                                        <p><input type="text" ref={el => this[i] = el} className="comp_count" disabled value={item.count} /></p>
                                                        <div className="comp_btn btn1" onClick={() => this.changeCount(item.goodsName, -1, i)}>-</div>
                                                        <div className="comp_btn btn2" onClick={() => this.changeCount(item.goodsName, 1, i)}>+</div>
                                                    </div>
                                                </a>
                                            </AgreeItem>
                                        </Flex.Item>
                                    </Flex>

                                </div>
                            ))
                        }
                    </div>
                    <div className="box_by">
                        <Flex>
                            <Flex.Item>
                                <AgreeItem className="ckechquan" data-seed="logId" onChange={e => console.log('checkbox', e)}>

                                    <a onClick={(e) => { e.preventDefault() }}>
                                        <span>删除</span>

                                        <div className="jiesuan">
                                            <div className="total">
                                                <span>合计:</span>
                                                <span>￥0</span>
                                            </div>
                                            <div className="by_goods">结算(45)</div>
                                        </div>
                                    </a>
                                </AgreeItem>
                            </Flex.Item>
                        </Flex>
                    </div>


                </div>


                {
                    !car.cartInfo &&
                    <div className="no">
                        <img className="img" src={cartImg} alt="" />

                        <button className="carbtn" onClick={this.goToHome}>去首页逛逛</button>
                    </div>
                }


            </div >
        )
    }
}
export default Cart