import { observable, action, computed, autorun } from "mobx";
import { axios } from "&"

class Car {
    @observable cartInfo = []
    @observable count = ""
    @observable price = ""

    @action changeCount = async (goodsName, count, i) => {
        const res = await axios.post("/react/changecount", { goodsName, count, i })
      return  this.count = res.data.result.count, this.price = res.data.min_price

    }

    @action getCartInfo = async (mobile) => {
        const res = await axios.post("/react/getcarInfo", { mobile })
        console.log(res.data.result)
        return this.cartInfo = res.data.result

    }


}
export default new Car() 